import { mapState } from "vuex";
import Sidebar from "~/components/Sidebar.vue";
import PostList from "~/components/PostList.vue";
import ads from "~/components/ads.vue";

export default {
  head() {
    const tag = this.$store.state.tags.current;
    const title = `${tag.title}`;
    const description = `The most ${tag.title}`;
    const thumb = `${process.env.baseUrl}/thumb.jpg`;
    return {
      title: title,
      meta: [
        {
          hid: "og:site_name",
          name: "og:site_name",
          content: "travelcoffeehot.com",
        },
        {
          hid: "og:type",
          name: "og:type",
          content: "ComicSeries",
        },
        {
          hid: "og:description",
          name: "og:description",
          content: description,
        },
        {
          hid: "og:title",
          name: "og:title",
          content: title,
        },
        {
          hid: "og:image",
          name: "og:image",
          content: thumb,
        },

        {
          hid: "twitter:card",
          name: "twitter:card",
          content: "ComicSeries",
        },
        {
          hid: "twitter:description",
          name: "twitter:description",
          content: description,
        },
        {
          hid: "twitter:title",
          name: "twitter:title",
          content: title,
        },
        {
          hid: "twitter:image",
          name: "twitter:image",
          content: thumb,
        },

        {
          name: "tile",
          content: title,
        },
        {
          name: "image",
          content: thumb,
        },
        {
          name: "keywords",
          content: `${tag.title}`,
        },
        {
          name: "description",
          content: description,
        },
      ],
    };
  },
  watchQuery: ["page", "s"],
  async asyncData({ store, params, route }) {
    await store.dispatch("tags/fetchOne", {
      _id: params.id,
    });
    await store.dispatch("posts/fetchAll", {
      tag: store.state.tags.current._id,
    });
  },
  data: function () {
    const params = this.$route.query;
    return {
      params: params,
    };
  },
  components: {
    Sidebar,
    PostList,
    ads,
  },
  methods: {},
  computed: mapState(["posts", "tags"]),
};
